import '@/common/uni.css';
import api from '@/common/index.js';
export default {
	data() {
		return {
			form: {
				mid: '',
				gs: '',
				dh: ''
			}
		}
	},
	methods: {
		confirm() {
			let data = this.form;
			if (!data.mid) {
				this.$api.msg('请输入售后编号');
				return;
			}
			if (!data.gs) {
				this.$api.msg('请输入物流公司');
				return;
			}
			if (!data.dh) {
				this.$api.msg('请输入物流单号');
				return;
			}

			let that = this;
			api.post("/index.php/Home/User/txth", data, function(res) {
				that.$api.msg('提交成功');
				uni.navigateBack({
					delta: 1
				})
			})

		}
	}
}
